@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap');

.App{
  
  position: relative;
  -webkit-tap-highlight-color: transparent;  
  animation: emerge 1s ease-in-out;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden; 
  p{
    position:absolute;
    width: fit-content;
    height: fit-content;
    margin: auto;
    top:0;
    left:0;
    right:0;
    bottom:0;
  }
}


@keyframes emerge {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}


